	import { debounce } from '@/common/utils.js';
export default {
	data() {
		return {
			phone: uni.getStorageSync('phone'),
			isPassword: true,
			isPassword1: true,
			codename: ' 获取验证码 ',
			pwd: '',
			pwd1: '',
			n: true,
			iscode: '',
			code: '',
			disabled: false
		};
	},
	onBackPress(option) {
		plus.key.hideSoftKeybord();
	},
	computed: {
		allowLogin () {
			return !!(this.pwd && this.pwd1 && this.code)
		},
	},
	methods: {
		next: function() {
			var that = this;
			that.n = false;
		},
		back: function() {
			var that = this;
			that.n = true;
		},
		getLoginPassword: function(e) {
			this.pwd = e.detail.value;
		},
		checkPwd:function(e){
			this.pwd = e.detail.value;
			var str = /^[a-z0-9]{6,16}$/;
			if(!str.test(this.pwd)) {
				uni.showToast({
					title: '密码格式为6到16位字母或数字',
					icon: 'none',
					duration: 2000
				});
			}
		},
		getLoginPassword1: function(e) {
			this.pwd1 = e.detail.value;
		},
		getCodeValue: function(e) {
			this.code = e.detail.value;
		},
		show: function() {
			this.isPassword = !this.isPassword;
		},
		show1: function() {
			this.isPassword1 = !this.isPassword1;
		},
		getCode: function() {
			var _this = this;
			uni.request({
				//短信接口
				url: _this.url + 'users/login/sms/',
				method: 'POST',
				data: {
					mobile: this.phone
				},
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				success(res) {
					//根据code判断
					var ocode = res.statusCode;
					if (ocode == 200) {
						_this.iscode = res.data.data;
						var num = 61;
						var timer = setInterval(function() {
							num--;
							if (num <= 0) {
								clearInterval(timer);

								(_this.codename = '重新发送'), (_this.disabled = false);
							} else {
								_this.codename = num + 's';
								_this.disabled = true;
							}
						}, 1000);
					} else if (ocode == 411) {
						uni.showToast({
							title: '操作太频繁，请稍候重试',
							icon: 'none',
							duration: 2000
						});
						return false;
					}
				}
			});
		},
		//获取验证码
		linkToTransfer: debounce(function(e){
			this.getCode();
			var _this = this;
		},500, true),
		getCodeBtn: function(e) {
			this.linkToTransfer(e)
		},
		save() {
			var _self = this;
			if (this.pwd == '') {
				uni.showToast({
					title: '请输入登录密码',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.pwd1 == '') {
				uni.showToast({
					title: '请确认登录密码',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.code && this.code != this.iscode) {
				uni.showToast({
					title: '验证码错误',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.pwd1 && this.pwd && this.pwd1 !== this.pwd) {
				uni.showToast({
					title: '两次密码不一致',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			var str = /^[a-z0-9]{6,16}$/;
			if (!str.test(this.pwd)) {
				uni.showToast({
					title: '密码格式不正确',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.code == '') {
				uni.showToast({
					title: '验证码不能为空',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			uni.request({
				url: this.url + 'updataLoginpwdcode/',
				method: 'POST',
				data: {
					mobile: this.phone,
					password: this.pwd,
					password1: this.pwd1,
					code: this.code
				},
				headers: {
					'Content-Type': 'application/json'
				},
				success: res => {
					if (res.statusCode == 200) {
						uni.showToast({
							title: '登录密码修改成功',
							icon: 'none',
							duration: 2000
						});
						uni.removeStorageSync('phone');
						uni.removeStorageSync('token');
						uni.removeStorageSync('nowtime');
						uni.reLaunch({
							url: '../../pages/login/login'
						});
					}
					if (res.statusCode == 400) {
						uni.showToast({
							title: '验证码已过期',
							icon: 'none'
						});
					}
				}
			});
		}
	}
};
